import { css } from '@emotion/react';
import Image from 'next/image';

import { Typography } from 'src/components/dataDisplay';
import { FullHeightBox, Stack } from 'src/components/layout';
import { ScrollSnapAlignedPaper } from 'src/components/surface';
import { IMAGES_PATH } from 'src/constants/resouce';
import useGameLink, { WORDLE, WORDLE2 } from 'src/hooks/useGameLink';
import useProductInfo from 'src/hooks/useProductInfo';

const contentLayout = css`
  padding: 40px 20px;
`;

const title = css`
  text-align: center;
  color: #222222;
`;

const titleDescription = css`
  display: block;
  text-align: center;
  color: #222222;
`;

const stampContainer = css`
  width: 160px;
  padding: 20px 40px;
  display: flex;
  align-items: center;
  background: #edfbee;
  border-radius: 40px;
`;

const buttonDescription = css`
  text-align: center;
  color: #888888;
`;

const button = css`
  height: 48px;
  padding-top: 15px;
  line-height: 20px;
  padding-bottom: 13px;
  border: none;
  border-radius: 8px;
  color: #ffffff;
  background: #28b978;
`;

const fullWidth = css`
  width: 100%;
`;

const StampPaper = (): JSX.Element => {
  const { readableName: brandName } = useProductInfo();
  const gameLink = useGameLink(WORDLE);
  const gameDeepLink = useGameLink(WORDLE2);
  const nextAction = {
    text: '게임존으로 넘어가기',
    link: '#',
    description: '브랜드제품존을 모두 구경했다면?',
  };
  const handleClick = () => {
    window.location.href = gameLink;
  };
  const handleDeepClick = () => {
    window.location.href = gameDeepLink;
  };
  return (
    <ScrollSnapAlignedPaper>
      <FullHeightBox>
        <Stack
          alignItems="center"
          justifyContent="space-between"
          fullWidth
          css={contentLayout}
        >
          <Stack alignItems="center" spacing={80}>
            <Stack spacing={12}>
              <Typography variant="h3" css={title}>
                {brandName}의 스토리, 어땠나요?
              </Typography>
              <div>
                <Typography variant="title" css={titleDescription}>
                  {brandName}과 한층 더 가까워지셨다면
                </Typography>
                <Typography variant="title" css={titleDescription}>
                  아래 도장을 꾹 눌러서 스탬프를 받아가세요!
                </Typography>
              </div>
            </Stack>
            <div css={[stampContainer]}>
              <Image
                alt="stamp"
                src={`${IMAGES_PATH}stamp.png`}
                width={160}
                height={160}
              />
            </div>
          </Stack>
          <Stack spacing={12} fullWidth>
            <Typography variant="body" css={buttonDescription}>
              {nextAction.description}
            </Typography>
            <button
              css={[button, fullWidth]}
              type="button"
              onClick={handleDeepClick}
            >
              {nextAction.text} - {gameLink}
            </button>
            <button
              css={[button, fullWidth]}
              type="button"
              onClick={handleClick}
            >
              {nextAction.text} - {gameDeepLink}
            </button>
          </Stack>
        </Stack>
      </FullHeightBox>
    </ScrollSnapAlignedPaper>
  );
};

export default StampPaper;
