import type { IconDynamicSizeProps } from './IconDynamicSize';
import IconDynamicSize from './IconDynamicSize';

const DEFAULT = {
  ALT: 'brand-logo',
  WIDTH: 161,
  HEIGHT: 24,
} as const;

interface BrandLogoProps
  extends Pick<IconDynamicSizeProps, 'src' | 'width' | 'height'> {}

const BrandLogo = ({
  src,
  width,
  height,
}: BrandLogoProps): JSX.Element | null => {
  const defaultSize = { width: DEFAULT.WIDTH, height: DEFAULT.HEIGHT };
  return (
    <IconDynamicSize
      alt={DEFAULT.ALT}
      src={src}
      width={width}
      height={height}
      defaultSize={defaultSize}
    />
  );
};

export default BrandLogo;
