import { IMAGES_PATH } from 'src/constants/resouce';

import type { IconDynamicSizeProps } from './IconDynamicSize';
import IconDynamicSize from './IconDynamicSize';

const DEFAULT = {
  ALT: 'hwahea-logo',
  SRC: `${IMAGES_PATH}icon/hwahea.svg`,
  WIDTH: 55,
  HEIGHT: 32,
} as const;

interface LogoProps extends Pick<IconDynamicSizeProps, 'width' | 'height'> {}

const Logo = ({ width, height }: LogoProps): JSX.Element | null => {
  const defaultSize = { width: DEFAULT.WIDTH, height: DEFAULT.HEIGHT };
  return (
    <IconDynamicSize
      alt={DEFAULT.ALT}
      src={DEFAULT.SRC}
      width={width}
      height={height}
      defaultSize={defaultSize}
    />
  );
};

export default Logo;
