import { css } from '@emotion/react';

import { AnimatedInView, LEFT_TO_RIGHT_OPTIONS } from 'src/components/animated';
import { FullHeightBox, Stack } from 'src/components/layout';
import { ScrollSnapAlignedPaper } from 'src/components/surface';
import type {
  BadgeContent,
  NormalMagazineItem,
} from 'src/hooks/useMagazineItems';

import Badge from './Badge';
import { Marker } from './Marker';
import { getContainerStyle } from './styles';
import Typographies from './Typographies';

const white = css`
  color: #ffffff;
`;

interface AnimatedBadgeProps extends Partial<BadgeContent> {}

const AnimatedBadge = ({
  variant,
  content,
  hasBorder = false,
}: AnimatedBadgeProps): JSX.Element | null => {
  if (!variant || !content) {
    return null;
  }
  return (
    <AnimatedInView options={LEFT_TO_RIGHT_OPTIONS}>
      <Badge variant={variant} hasBorder={hasBorder}>
        {content}
      </Badge>
    </AnimatedInView>
  );
};

interface NormalMagazinePaperProps extends NormalMagazineItem {}

const MagazinePaper = ({
  imageUrl,
  layoutType,
  staticContent,
  linkableContents,
}: NormalMagazinePaperProps): JSX.Element => {
  const { contentLayout, titleStyle, descriptionStyle } =
    getContainerStyle(layoutType);

  return (
    <ScrollSnapAlignedPaper backgroundUrl={imageUrl}>
      <FullHeightBox css={white}>
        <Stack spacing={12} css={contentLayout}>
          <AnimatedBadge
            content={staticContent.badge?.content}
            variant={staticContent.badge?.variant}
            hasBorder={staticContent.badge?.hasBorder}
          />

          <AnimatedInView>
            <Typographies
              name="title"
              contentAndVariant={staticContent.title}
              isUpperCase
              css={titleStyle}
            />
          </AnimatedInView>

          <AnimatedInView>
            <Typographies
              name="description"
              contentAndVariant={staticContent?.description}
              css={descriptionStyle}
            />
          </AnimatedInView>
        </Stack>

        {linkableContents?.map((content, index) => {
          const key = `linker-${index}`;
          return <Marker key={key} {...content} />;
        })}
      </FullHeightBox>
    </ScrollSnapAlignedPaper>
  );
};

export default MagazinePaper;
