import { css } from '@emotion/react';

import { Animated, REPEAT_OPTIONS } from 'src/components/animated';
import { Typography } from 'src/components/dataDisplay';
import { BrandLogo, CollaborationIcon, Logo } from 'src/components/icons';
import { FullHeightBox, Stack } from 'src/components/layout';
import { Paper } from 'src/components/surface';
import useProductInfo from 'src/hooks/useProductInfo';
import useSurveysForBestFit from 'src/hooks/useSurveysForBestFit';

const contentLayout = css`
  text-align: center;
  color: #ffffff;
`;

const BestFitProductLoadingPaper = (): JSX.Element => {
  const { name: brandName, logoUrl } = useProductInfo();
  const { backgroundImage } = useSurveysForBestFit({ brandName });

  return (
    <Paper backgroundUrl={backgroundImage}>
      <FullHeightBox>
        <Stack
          spacing={20}
          justifyContent="center"
          alignItems="center"
          fullWidth
          css={contentLayout}
        >
          <Stack direction="row" spacing={10} alignItems="center">
            <Logo height={20} />
            <CollaborationIcon />
            <BrandLogo src={logoUrl} height={20} />
          </Stack>
          <Animated options={REPEAT_OPTIONS}>
            <Typography variant="h3">결과를 확인하고 있어요</Typography>
          </Animated>
        </Stack>
      </FullHeightBox>
    </Paper>
  );
};

export default BestFitProductLoadingPaper;
