import type { IconBaseProps } from './IconBase';
import IconBase from './IconBase';

const isValidNumber = (value: unknown): boolean => {
  return Number.isInteger(value) && Number(value) > 0;
};

type SizeProp = Partial<{
  width: number;
  height: number;
}>;

interface HasNoSizeProps {
  (props: SizeProp): boolean;
}

const hasNoSizeProps: HasNoSizeProps = ({ width, height }) => {
  return !Number.isInteger(width) && !Number.isInteger(height);
};

interface IsRateRequired {
  (props: SizeProp): boolean;
}

const isRateRequired: IsRateRequired = ({ width, height }) => {
  return !(Number.isInteger(width) && Number.isInteger(height));
};

interface GetRateProps {
  current?: number;
  base: number;
}
interface GetRate {
  (props: GetRateProps): number;
}

const getRate: GetRate = ({ current, base }) => {
  if (!isValidNumber(current) || !isValidNumber(base)) {
    return 0;
  }

  return Number(current) / base;
};

export interface IconDynamicSizeProps
  extends Pick<IconBaseProps, 'src' | 'alt'>,
    Partial<Pick<IconBaseProps, 'width' | 'height'>> {
  defaultSize: {
    width: number;
    height: number;
  };
}

const IconDynamicSize = ({
  alt,
  src,
  width,
  height,
  defaultSize,
}: IconDynamicSizeProps): JSX.Element | null => {
  if (!width && !height) {
    // width, height 모두 없음
    return (
      <IconBase
        alt={alt}
        src={src}
        width={defaultSize.width}
        height={defaultSize.height}
      />
    );
  }

  if (!!width && !!height) {
    // width, height 모두 있음
    return <IconBase alt={alt} src={src} width={width} height={height} />;
  }

  const rate =
    getRate({ current: Number(width), base: defaultSize.width }) ||
    getRate({ current: Number(height), base: defaultSize.height });
  if (rate <= 0) {
    return null;
  }

  const currentWidth = width ?? defaultSize.width * rate;
  const currentHeight = height ?? defaultSize.height * rate;
  return (
    <IconBase alt={alt} src={src} width={currentWidth} height={currentHeight} />
  );
};

export default IconDynamicSize;
