import { css } from '@emotion/react';

import type { PaperProps } from './Paper';
import Paper from './Paper';

const scrollableItem = css`
  position: relative;
  scroll-snap-stop: always;
  scroll-snap-align: center;
`;

interface ScrollSnapAlignedPaperProps extends PaperProps {}

const ScrollSnapAlignedPaper = ({
  children,
  backgroundUrl,
  ...others
}: ScrollSnapAlignedPaperProps): JSX.Element => {
  return (
    <Paper css={scrollableItem} backgroundUrl={backgroundUrl} {...others}>
      {children}
    </Paper>
  );
};

export default ScrollSnapAlignedPaper;
