import { ROOT_PATH } from 'src/constants/resouce';

export const isHttpProtocol = (href: string) =>
  Boolean(href.match(/^(http|https):\/\//i));

export const isInRoot = (href: string) => Boolean(href.startsWith(ROOT_PATH));

export const getHrefExcludeRootPath = (href: string) => {
  if (!isInRoot(href)) {
    return href;
  }
  return href.substring(ROOT_PATH.length);
};

const WEBVIEW_PATH = 'popupstore-web';
const WEBVIEW_PROTOCOL = 'hwahae';

export const getWebviewLink = (href: string): string =>
  `${WEBVIEW_PROTOCOL}://${WEBVIEW_PATH}?url=${href}`;
